import React from "react";

export interface InputProps {
  placeholder: string | null;
  name: string;
}

function Input({ placeholder = null, name }: InputProps) {
  return (
    <div className="input">
      <input
        type="text"
        name={name}
        placeholder={`${placeholder || "Im an input text"}`}
      />
    </div>
  );
}

export default Input;
